import React from "react"
import {themeLabel} from "../styles/theme-selector.module.css"

type ThemeSelectorProps = {
    defaultTheme: string,
    handleChange: any,
    isMobile: boolean
}

const ThemeSelector = ({defaultTheme, handleChange, isMobile}: ThemeSelectorProps) => {
  return (
    <>
    <label className={themeLabel}>Theme:</label>
        <select name="theme" id="theme" defaultValue={!isMobile? "book": "light"} onChange={handleChange}>
        {!isMobile && <option value="book">Book</option>}
        <option value="light">Light</option>
        <option value="dark">Dark</option>
      </select>
    </>
  )
}

export default ThemeSelector