import React, {useEffect}from "react"
import type { HeadFC, PageProps } from "gatsby"
import Standard from "../components/standard/standard";
import Book from "../components/book/book";
import ThemeSelector from "../components/theme-selector";
import {index, header} from "../styles/index.module.css"
import useLocalStorage from "use-local-storage";

let isMobile = false
  if(typeof window !== "undefined" && window.navigator.maxTouchPoints > 1)
    isMobile = true

const IndexPage: React.FC<PageProps> = () => {

  let defaultTheme = "book"
  if(isMobile)
    defaultTheme = "light"
  const [theme, setTheme] = useLocalStorage("theme", defaultTheme);
  return (
    <div className={index} data-theme={theme}>
      {typeof window !== "undefined" && <><header className={header}>
        <ThemeSelector defaultTheme={theme} isMobile={isMobile} handleChange={
          (e: React.ChangeEvent<HTMLInputElement>) => setTheme(e.target.value)} />
      </header>
      {theme.includes("book") ? <Book/> : <Standard/>} </>}
    </div>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Portfolio</title>
